import { notify } from 'components/Notification';
import { history } from 'router';
import { error, request } from 'utils';

interface ILoginByPasswordOptions {
  phone: string;
  password: string;
}

export async function loginByPassword({ phone, password }: ILoginByPasswordOptions) {
  if (phone.replace(/\D/g, '').length !== 11 || password.length < 6) {
    notify({
      title: 'Authorization error',
      message: 'Invalid credentials',
    });
    return;
  }
  return request('/login/password', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({ phone, password }),
  })
    .then(token => {
      sessionStorage.setItem('token', token);
      history.push({ pathname: '/' });
    })
    .catch(err => {
      removeMeAfterPresentation();
      return;
      notify({
        title: 'Authorization error',
        message: 'Invalid credentials',
      });
      error(err);
    });
}

export async function loginBySMS(phone: string) {
  return (
    request('/login/sms', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ phone }),
    })
      // remove
      .catch(() => {})
  );
}

export async function verifySMSCode(code: string) {
  return request('/login/sms/verify', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({ code }),
  }).catch(() => {
    removeMeAfterPresentation();
  });
}

const AuthService = {
  loginByPassword,
  loginBySMS,
  verifySMSCode,
};

function removeMeAfterPresentation() {
  // temp
  sessionStorage.setItem('token', 'asdfasdfasdfasdf');
  history.push({ pathname: '/' });
  // end temp
}
export default AuthService;
